<template>
  <VModal class="justify-content-end" position="right" :show="show">
    <form @submit.prevent="onSubmit" autocomplete="off">
      <ModalContainer
        v-if="show"
        :title="title"
        @close="$emit('close', $event)"
      >
        <template v-slot:content>
          <TextInput
            :label="localize.tabCommercialOrganizationsModalAddNameCompany.text"
            popover-position="bottom-center"
            :maxlength="100"
            input-type="contrast"
            :error="errors.russianName"
            :placeholder="localize.commonFormPlaceholderRus.text"
            v-model="organization.russianName"
            name="organization.russianName"
            root-class="mb-2"
          >
            <template v-slot:popover>
              <PopoverContent :width="266" class="organization-modal__popover">
                <VTypography font-weight="medium">{{
                  localize.tabCommercialOrganizationsModalAddNameCompany.prompt
                }}</VTypography>
              </PopoverContent>
            </template>
          </TextInput>

          <TextInput
            :maxlength="100"
            input-type="contrast"
            :error="errors.englishName"
            v-model="organization.englishName"
            name="organization.englishName"
            :placeholder="localize.commonFormPlaceholderEng.text"
            root-class="mb-5"
          />

          <TextInput
            :label="
              localize.tabCommercialOrganizationsModalAddOrganizationContact
                .text
            "
            :maxlength="100"
            input-type="contrast"
            :error="errors.russianContact"
            :placeholder="localize.commonFormPlaceholderRus.text"
            v-model="organization.russianContact"
            name="organization.russianContact"
            popover-position="top-center"
            root-class="mb-2"
          >
            <template v-slot:popover>
              <PopoverContent :width="266" class="organization-modal__popover">
                <VTypography font-weight="medium">{{
                  localize.tabCommercialOrganizationsModalAddOrganizationContact
                    .prompt
                }}</VTypography>
              </PopoverContent>
            </template>
          </TextInput>

          <TextInput
            :maxlength="100"
            input-type="contrast"
            :error="errors.englishContact"
            v-model="organization.englishContact"
            name="organization.englishContact"
            :placeholder="localize.commonFormPlaceholderEng.text"
          />
        </template>

        <template v-slot:footer>
          <ActionButton type="submit" button-type="outline">{{
            title
          }}</ActionButton>
        </template>
      </ModalContainer>
    </form>
  </VModal>
</template>

<script>
import VModal from "@/components/modals/Modal";
import TextInput from "@/components/ui/TextInput";
import ActionButton from "@/components/buttons/ActionButton";
import ModalContainer from "@/components/modals/ModalContainer";
import PopoverContent from "@/components/popover/PopoverContent";
import VTypography from "@/components/ui/Typography";
import { mapGetters } from "vuex";
import { formatOrganizationErrors } from "@/helpers/FormsHelpers";

export default {
  name: "OrganizationModal",
  components: {
    VTypography,
    PopoverContent,
    TextInput,
    ActionButton,
    ModalContainer,
    VModal,
  },
  emits: {
    close: Function,
    submit: () => true,
  },
  props: {
    show: Boolean,
    initialValues: {
      type: Object,
      default: () => ({}),
    },
  },
  data() {
    return {
      errors: {},
      organization: {},
    };
  },
  computed: {
    ...mapGetters("i18n", ["localize"]),
    title() {
      const hasRussianName = !!this.initialValues?.russianName;

      return hasRussianName
        ? this.localize.commonFormOrganizationChange.text
        : this.localize.commonFormOrganizationAdd.text;
    },
  },
  watch: {
    show(nextValue, prevValue) {
      if (nextValue !== prevValue) {
        this.organization = { ...this.initialValues };
      }
    },
  },
  methods: {
    onSubmit(event) {
      this.errors = formatOrganizationErrors(this.organization, this.localize);

      if (Object.keys(this.errors).length === 0) {
        this.$emit("submit", this.organization);
        this.organization = {};

        event.target.reset();
      }
    },
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/variables.scss";

.organization-modal__popover {
  background-color: $gray3;
}
</style>
