<template>
  <FormsContainer>
    <TypographyWithInfo font-weight="heavy" font-style="header-1" class="mb-5"
      >{{ localize.tabCommercialOrganizations.text }}
      <template v-slot:popover>
        <PopoverContent :width="350">
          <VTypography font-weight="medium">{{
            localize.tabCommercialOrganizations.prompt
          }}</VTypography>
        </PopoverContent>
      </template>
    </TypographyWithInfo>

    <OrganizationsTable
      v-if="organizations.length > 0"
      :editable="editable"
      class="mb-5"
      :list="organizations"
      @delete="onDelete"
      @edit="onEdit"
    />

    <AddButton v-if="editable" @click="showModal = true">
      {{ localize.commonFormOrganizationAdd.text }}
    </AddButton>

    <OrganizationModal
      :show="showModal"
      @submit="onSubmit"
      @close="onCloseModal"
      :initial-values="editedOrganization"
    />
  </FormsContainer>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";

import VTypography from "@/components/ui/Typography";
import AddButton from "@/components/buttons/AddButton";
import FormsContainer from "@/components/forms/FormsContainer";
import PopoverContent from "@/components/popover/PopoverContent";
import TypographyWithInfo from "@/components/ui/TypographyWithInfo";
import OrganizationModal from "@/components/organizations/OrganizationModal";
import OrganizationsTable from "@/components/organizations/OrganizationsTable";
import { FormsTabKeys } from "@/constants/FormConstants";

export default {
  name: "OrganizationsForm",
  components: {
    VTypography,
    PopoverContent,
    OrganizationModal,
    AddButton,
    FormsContainer,
    OrganizationsTable,
    TypographyWithInfo,
  },
  data() {
    return {
      showModal: false,
      editedOrganization: undefined,
      editedOrganizationIndex: undefined,
    };
  },
  computed: {
    ...mapGetters("i18n", ["localize"]),
    ...mapGetters("formInfo", ["canEditForm"]),
    ...mapGetters("organizations", {
      organizations: "getOrganizations",
    }),
    editable() {
      return this.canEditForm(FormsTabKeys.Organizations);
    },
  },
  methods: {
    ...mapMutations("organizations", [
      "addOrganization",
      "removeOrganization",
      "updateOrganization",
    ]),
    onSubmit(organization) {
      if (this.editedOrganizationIndex >= 0) {
        this.updateOrganization({
          index: this.editedOrganizationIndex,
          organization,
        });
      } else {
        this.addOrganization(organization);
      }

      this.showModal = false;
      this.editedOrganization = undefined;
      this.editedOrganizationIndex = undefined;

      this.$emit("success");
    },
    onEdit(index) {
      this.editedOrganizationIndex = index;
      this.editedOrganization = {
        ...this.organizations[index],
      };
      this.showModal = true;
    },
    onDelete(index) {
      this.removeOrganization(index);
    },
    onCloseModal() {
      this.showModal = false;
      this.editedOrganization = undefined;
      this.editedOrganizationIndex = undefined;
    },
  },
};
</script>
