<template>
  <div class="container">
    <TypographyWithInfo font-weight="heavy" class="mb-5">
      {{ localize.tabResourceBlkInstituteResources.text }}

      <template v-slot:popover>
        <PopoverContent :width="684">
          <VTypography font-weight="medium"
            >{{ localize.tabResourceBlkInstituteResources.prompt }}
          </VTypography>
        </PopoverContent>
      </template>
    </TypographyWithInfo>

    <RadioGroup
      class="mb-5"
      :disabled="!editable"
      v-model="usedInternalResources"
      :items="[
        { key: true, title: localize.commonFormYes.text },
        { key: false, title: localize.commonFormNo.text },
      ]"
    />

    <div v-if="usedInternalResources" class="row">
      <div class="col">
        <TextInput
          :disabled="!editable"
          v-model="russianInternalResources"
          :placeholder="localize.commonFormPlaceholderRus.text"
          :label="localize.tabResourceBlkInstituteResourceQst?.text || '&nbsp;'"
        >
          <template v-slot:popover>
            <PopoverContent :width="350">
              {{ localize.tabResourceBlkInstituteResourceQst?.prompt }}
            </PopoverContent>
          </template>
        </TextInput>
      </div>
      <div class="col">
        <TextInput
          root-class="mt-4"
          :disabled="!editable"
          :placeholder="localize.commonFormPlaceholderEng.text"
          v-model="englishInternalResources"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";

import TextInput from "@/components/ui/TextInput";
import RadioGroup from "@/components/ui/RadioGroup";
import VTypography from "@/components/ui/Typography";
import PopoverContent from "@/components/popover/PopoverContent";
import TypographyWithInfo from "@/components/ui/TypographyWithInfo";

export default {
  name: "ResourceInternalSource",
  components: {
    TextInput,
    RadioGroup,
    VTypography,
    PopoverContent,
    TypographyWithInfo,
  },
  props: {
    editable: Boolean,
  },
  methods: {
    ...mapMutations("resources", [
      "changeUsedInternalResources",
      "changeRussianInternalResource",
      "changeEnglishInternalResource",
    ]),
  },
  computed: {
    ...mapGetters("i18n", ["localize"]),
    ...mapGetters("resources", [
      "getUsedInternalResources",
      "getEnglishInternalResources",
      "getRussianInternalResources",
    ]),
    usedInternalResources: {
      get() {
        return this.getUsedInternalResources;
      },
      set(value) {
        this.changeUsedInternalResources(value);
      },
    },
    russianInternalResources: {
      get() {
        return this.getRussianInternalResources;
      },
      set(value) {
        this.changeRussianInternalResource(value);
      },
    },
    englishInternalResources: {
      get() {
        return this.getEnglishInternalResources;
      },
      set(value) {
        this.changeEnglishInternalResource(value);
      },
    },
  },
};
</script>
