<template>
  <div class="d-flex flex-column">
    <TypographyWithInfo font-weight="heavy" class="mb-5">
      {{ localize.tabResourceBlkExternalFundingSource.text }}

      <template v-slot:popover>
        <PopoverContent :width="684">
          <VTypography font-weight="medium"
            >{{ localize.tabResourceBlkExternalFundingSource.prompt }}
          </VTypography>
        </PopoverContent>
      </template>
    </TypographyWithInfo>

    <RadioGroup
      class="mb-5"
      :disabled="!editable"
      v-model="usedExternalResources"
      :items="[
        { key: true, title: localize.commonFormApplied.text },
        { key: false, title: localize.commonFormNotUsed.text },
      ]"
    />

    <ResourcesExternalTable
      class="mb-5"
      @edit="onEdit"
      :list="resources"
      @delete="onDelete"
      :editable="editable"
      v-if="usedExternalResources && resources.length > 0"
    />

    <AddButton
      @click="showModal = true"
      v-if="usedExternalResources && editable"
    >
      {{ localize.commonFormAuthorAddSource.text }}
    </AddButton>

    <ResourcesExternalModal
      @close="onClose"
      :show="showModal"
      @submit="onSubmit"
      :initial-values="editedResource"
    />
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";

import RadioGroup from "@/components/ui/RadioGroup";
import VTypography from "@/components/ui/Typography";
import AddButton from "@/components/buttons/AddButton";
import PopoverContent from "@/components/popover/PopoverContent";
import TypographyWithInfo from "@/components/ui/TypographyWithInfo";
import ResourcesExternalTable from "@/components/resources/ResourcesExternalTable";
import ResourcesExternalModal from "@/components/resources/ResourcesExternalModal";

export default {
  name: "ResourceExternalSource",
  components: {
    AddButton,
    RadioGroup,
    VTypography,
    PopoverContent,
    TypographyWithInfo,
    ResourcesExternalModal,
    ResourcesExternalTable,
  },
  data() {
    return {
      showModal: false,
      editedResource: undefined,
      editedResourceIndex: undefined,
    };
  },
  props: {
    editable: Boolean,
  },
  methods: {
    ...mapMutations("resources", [
      "addExternalResource",
      "removeExternalResource",
      "updateExternalResource",
      "changeUsedExternalResources",
    ]),
    onClose() {
      this.showModal = false;
      this.editedResource = undefined;
      this.editedResourceIndex = undefined;
    },
    onSubmit(resource) {
      if (this.editedResourceIndex >= 0) {
        this.updateExternalResource({
          index: this.editedResourceIndex,
          resource,
        });
      } else {
        this.addExternalResource(resource);
      }

      this.onClose();

      this.$emit("success");
    },
    onEdit(index) {
      this.editedResourceIndex = index;
      this.editedResource = this.resources[index];
      this.showModal = true;
    },
    onDelete(index) {
      this.removeExternalResource(index);
    },
  },
  computed: {
    ...mapGetters("i18n", ["localize"]),
    ...mapGetters("resources", {
      resources: "getExternalResources",
      getUsedExternalResources: "getUsedExternalResources",
    }),
    usedExternalResources: {
      get() {
        return this.getUsedExternalResources;
      },
      set(value) {
        this.changeUsedExternalResources(value);
      },
    },
  },
};
</script>
