<template>
  <div class="d-flex flex-column pt-6 pb-2 px-0">
    <ItemPageLegendItem v-if="!complete" status="not-filled" class="mb-3" />
    <ItemPageLegendItem
      class="mb-6"
      v-if="!complete"
      status="not-fully-filled"
    />

    <VCheckbox v-if="complete" v-model="agreement" class="mb-3">
      <VTypography
        font-style="text-2"
        class="v-checkbox__text"
        v-html="agreementTitle"
      />
    </VCheckbox>

    <VPopover
      :disabled="!disabled"
      trigger-type="hover"
      position="top-center"
      class="item-page-footer__popover"
    >
      <template v-slot:content>
        <ActionButton
          :disabled="disabled"
          @click="$emit('send')"
          button-type="secondary-outline"
          class="item-page-footer_submit-button"
          >{{ localize.commonFormSendFormBtn.text }}</ActionButton
        >
      </template>

      <template v-slot:popover>
        <PopoverContent :width="234">
          <VTypography>{{ localize.commonFormSendFormBtn.prompt }}</VTypography>
        </PopoverContent>
      </template>
    </VPopover>
  </div>
</template>

<script>
import VCheckbox from "@/components/ui/Checkbox";
import VPopover from "@/components/popover/Popover";
import VTypography from "@/components/ui/Typography";
import ActionButton from "@/components/buttons/ActionButton";
import PopoverContent from "@/components/popover/PopoverContent";
import ItemPageLegendItem from "@/components/item-page/ItemPageLegendItem";
import { mapGetters } from "vuex";
import { formatString } from "@/utils/FormatUtils";

export default {
  name: "ItemPageFooter",
  components: {
    PopoverContent,
    VPopover,
    VCheckbox,
    ActionButton,
    ItemPageLegendItem,
    VTypography,
  },
  emits: {
    send: Function,
  },
  props: {
    complete: Boolean,
  },
  data() {
    return {
      agreement: false,
    };
  },
  computed: {
    ...mapGetters("i18n", ["localize"]),
    disabled() {
      return !this.complete || !this.agreement;
    },
    agreementTitle() {
      return formatString(
        this.localize.commonFormSendFormCheckbox.title,
        this.localize.commonFormSendFormCheckbox.params
      );
    },
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/variables.scss";

.item-page-footer_submit-button {
  max-width: 234px;
}

.v-checkbox__link {
  color: $gray1;
}

.item-page-footer__popover {
  width: 234px;
}
</style>
