<template>
  <VModal position="right" :show="show">
    <form ref="resourceExternalModalForm" @submit.prevent="onSubmit">
      <ModalContainer v-if="show" :title="title" @close="onClose">
        <template v-slot:content>
          <TextInput
            :maxlength="100"
            root-class="mb-2"
            input-type="contrast"
            :label="
              localize.tabResourceModalAddExternalFundingSourceProjectName.text
            "
            :error="errors.russianName"
            :placeholder="localize.commonFormPlaceholderRus.text"
            name="resource.russianName"
            v-model="resource.russianName"
            popover-position="bottom-center"
          >
            <template v-slot:popover>
              <PopoverContent
                :width="266"
                class="resources-external-modal__popover"
              >
                <VTypography font-weight="medium">{{
                  localize.tabResourceModalAddExternalFundingSourceProjectName
                    .prompt
                }}</VTypography>
              </PopoverContent>
            </template>
          </TextInput>

          <TextInput
            :maxlength="100"
            root-class="mb-5"
            input-type="contrast"
            name="resource.englishName"
            :error="errors.englishName"
            :placeholder="localize.commonFormPlaceholderEng.text"
            v-model="resource.englishName"
          />

          <TextInput
            :maxlength="8"
            :multiline="false"
            :label="
              localize.tabResourceModalAddExternalFundingSourceProjectNum.text
            "
            input-type="contrast"
            :show-counter="false"
            :error="errors.projectNumber"
            name="resource.projectNumber"
            v-model="resource.projectNumber"
            root-class="mb-2 resources-external-modal__project-number mb-5"
          >
            <template v-slot:startAdornment>
              <VTypography
                font-weight="medium"
                class="align-self-center ms-2 resources-external-modal__start-adornment"
                >№</VTypography
              >
            </template>
            <template v-slot:popover>
              <PopoverContent
                :width="266"
                class="resources-external-modal__popover"
              >
                <VTypography font-weight="medium">{{
                  localize.tabResourceModalAddExternalFundingSourceProjectNum
                    .prompt
                }}</VTypography>
              </PopoverContent>
            </template>
          </TextInput>

          <TypographyWithInfo
            font-weight="medium"
            class="mb-1 text-white"
            type="contrast"
          >
            {{
              localize.tabResourceModalAddExternalFundingSourceAgreementDate
                .text
            }}

            <template v-slot:popover>
              <PopoverContent
                :width="266"
                class="resources-external-modal__popover"
              >
                <VTypography font-weight="medium">{{
                  localize.tabResourceModalAddExternalFundingSourceAgreementDate
                    .prompt
                }}</VTypography>
              </PopoverContent>
            </template>
          </TypographyWithInfo>

          <DatePicker
            class="mb-5"
            name="resource.date"
            v-model="resource.date"
            :error="errors.date"
            input-class="text-white"
            input-type="contrast"
          />

          <TextInput
            :maxlength="8"
            :multiline="false"
            :show-counter="false"
            input-type="contrast"
            :label="
              localize.tabResourceModalAddExternalFundingSourceContractNum.text
            "
            name="resource.contractNumber"
            :error="errors.contractNumber"
            v-model="resource.contractNumber"
            root-class="mb-2 resources-external-modal__project-number mb-5"
          >
            <template v-slot:startAdornment>
              <VTypography
                font-weight="medium"
                class="align-self-center ms-2 resources-external-modal__start-adornment"
                >№</VTypography
              >
            </template>
            <template v-slot:popover>
              <PopoverContent
                :width="266"
                class="resources-external-modal__popover"
              >
                <VTypography font-weight="medium">{{
                  localize.tabResourceModalAddExternalFundingSourceContractNum
                    .prompt
                }}</VTypography>
              </PopoverContent>
            </template>
          </TextInput>

          <TextInput
            :maxlength="100"
            root-class="mb-2"
            input-type="contrast"
            :placeholder="localize.commonFormPlaceholderRus.text"
            :error="errors.russianFullName"
            name="resource.russianFullName"
            :label="
              localize.tabResourceModalAddExternalFundingSourceFullName.text
            "
            v-model="resource.russianFullName"
            popover-position="top-center"
          >
            <template v-slot:popover>
              <PopoverContent
                :width="266"
                class="resources-external-modal__popover"
              >
                <VTypography font-weight="medium">{{
                  localize.tabResourceModalAddExternalFundingSourceFullName
                    .prompt
                }}</VTypography>
              </PopoverContent>
            </template>
          </TextInput>

          <TextInput
            :maxlength="100"
            root-class="mb-5"
            input-type="contrast"
            name="resource.english"
            :placeholder="localize.commonFormPlaceholderEng.text"
            :error="errors.englishFullName"
            v-model="resource.englishFullName"
          />
        </template>

        <template v-slot:footer>
          <ActionButton type="submit" button-type="outline">{{
            title
          }}</ActionButton>
        </template>
      </ModalContainer>
    </form>
  </VModal>
</template>

<script>
import VModal from "@/components/modals/Modal";
import TextInput from "@/components/ui/TextInput";
import VTypography from "@/components/ui/Typography";
import DatePicker from "@/components/date-picker/DatePicker";
import ActionButton from "@/components/buttons/ActionButton";
import ModalContainer from "@/components/modals/ModalContainer";
import PopoverContent from "@/components/popover/PopoverContent";
import TypographyWithInfo from "@/components/ui/TypographyWithInfo";
import { mapGetters } from "vuex";
import { formatExternalResourceErrors } from "@/helpers/FormsHelpers";

export default {
  name: "ResourcesExternalModal",
  components: {
    VModal,
    TextInput,
    DatePicker,
    VTypography,
    ActionButton,
    ModalContainer,
    PopoverContent,
    TypographyWithInfo,
  },
  emits: {
    close: Function,
    submit: () => true,
  },
  props: {
    show: Boolean,
    initialValues: {
      type: Object,
      default: () => ({}),
    },
  },
  data() {
    return {
      errors: {},
      resource: {},
    };
  },
  computed: {
    ...mapGetters("i18n", ["localize"]),
    title() {
      return Object.keys(this.initialValues).length > 0
        ? this.localize.commonFormAuthorChangeSource.text
        : this.localize.commonFormAuthorAddSource.text;
    },
  },
  watch: {
    show(nextValue, prevValue) {
      if (nextValue !== prevValue) {
        this.resource = { ...this.initialValues };
      }
    },
  },
  methods: {
    onReset() {
      this.resource = {};

      const formRef = this.$refs.resourceExternalModalForm;

      if (formRef) {
        formRef.reset();
      }
    },
    onClose() {
      this.$emit("close");

      this.onReset();
    },
    onSubmit() {
      this.errors = formatExternalResourceErrors(this.resource, this.localize);

      if (Object.keys(this.errors).length === 0) {
        this.$emit("submit", this.resource);

        this.onClose();
      }
    },
  },
};
</script>

<style scoped lang="scss">
@import "~@/assets/styles/variables.scss";

.resources-external-modal__popover {
  background-color: $gray3;
}

.resources-external-modal__project-number {
  width: 169px;
}

.resources-external-modal__start-adornment {
  color: $gray4;
}
</style>
