<template>
  <FormsContainer class="mb-5">
    <VTypography font-weight="heavy" font-style="header-1" class="mb-5">{{
      localize.tabResources.text
    }}</VTypography>

    <ResourceExternalSource
      class="mb-7"
      :editable="editable"
      @success="$emit('success', $event)"
    />

    <ResourceInternalSource :editable="editable" />
  </FormsContainer>
</template>

<script>
import { mapGetters } from "vuex";

import VTypography from "@/components/ui/Typography";
import { FormsTabKeys } from "@/constants/FormConstants";
import FormsContainer from "@/components/forms/FormsContainer";
import ResourceExternalSource from "@/components/resources/ResourceExternalSource";
import ResourceInternalSource from "@/components/resources/ResourceInternalSource";

export default {
  name: "ResourcesForm",
  emits: ["success"],
  computed: {
    ...mapGetters("i18n", ["localize"]),
    ...mapGetters("formInfo", ["canEditForm"]),
    editable() {
      return this.canEditForm(FormsTabKeys.Resources);
    },
  },
  components: {
    VTypography,
    FormsContainer,
    ResourceInternalSource,
    ResourceExternalSource,
  },
};
</script>
